import React, {useState} from 'react';
import './kelas.css';

const Kelas = React.memo(({jenjang, handleClick}) => {
	const [state, setState] = useState('kelas__entering');

	setTimeout(() => {
		if( state === 'kelas__entering') setState('kelas');
	},200);

	const buttonClicked = (e) => {
		e.target.classList.add('kelas-button-clicked');
		setTimeout(() => {
			e.target.classList.remove('kelas-button-clicked');
		}, 100);
		setTimeout(() => {
			e.target.classList.add('kelas-button-clicked');
		}, 200);
		setTimeout(() => {
			e.target.classList.remove('kelas-button-clicked');
			setState('kelas__exiting');
		}, 300);
		setTimeout(() => {
			handleClick(e.target.value);
		},800)
	}

	const setKelasByJenjang = (jenjang, level) => {
		if(jenjang === 'SD' && level === 1) return '4 SD';
		if(jenjang === 'SD' && level === 2) return '5 SD';
		if(jenjang === 'SD' && level === 3) return '6 SD';
		if(jenjang === 'SMP' && level === 1) return '7 SMP';
		if(jenjang === 'SMP' && level === 2) return '8 SMP';
		if(jenjang === 'SMP' && level === 3) return '9 SMP';
		if(jenjang === 'SMA' && level === 1) return '10 SMA';
		if(jenjang === 'SMA' && level === 2) return '11 SMA';
		if(jenjang === 'SMA' && level === 3) return '12 SMA';
	}

	  return (
		   <div className={`${state}`}>
			   	<h2 className="kelas__question">Kamu kelas berapa?</h2>
		   	<div className="kelas__button-wrapper">
		   		{jenjang === 'SD' ? <button className="kelas__button" onClick={(e) => buttonClicked(e)} value="3 SD">3 SD</button> : null}
		   		<button className="kelas__button" onClick={(e) => buttonClicked(e)} value={setKelasByJenjang(jenjang, 1)}>{setKelasByJenjang(jenjang, 1)}</button>
		   		<button className="kelas__button" onClick={(e) => buttonClicked(e)} value={setKelasByJenjang(jenjang, 2)}>{setKelasByJenjang(jenjang, 2)}</button>
		   		<button className="kelas__button" onClick={(e) => buttonClicked(e)} value={setKelasByJenjang(jenjang, 3)}>{setKelasByJenjang(jenjang, 3)}</button>
		   	</div>
		   </div>
	  )
});

export default Kelas;